.image-editor-options {
    display: flex;
    flex-wrap: wrap;
    background: #f9f9f9;
    padding-top: 0.25rem;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    padding-bottom: 0.5rem;
    & > div:first-child {
      padding-right: 1rem;
    }
  }
  
  .story-item-link {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    margin-bottom: 10px;
    .components-base-control,
    .components-base-control__field  {
      margin-bottom: 0!important;
    }
    & > div:first-of-type {
      flex-grow: 1;
    }
    input.components-text-control__input {
      border-right: 0;
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
    }
    button.components-button.is-button.is-primary {
      height: 30px;
      border-left: 0;
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
    }
  }
  
  .story-label-select > .components-base-control__field {
     margin-bottom: 0;
  }
  
  .story-item {
    div.components-base-control__field {
      margin-bottom: 0;
    }
  }
  // .components-popover__content {
  //   padding-bottom: 0.5rem;
  // }
  // .components-datetime__time {
  //   display: none;
  // }
  
  @-webkit-keyframes fadeIn {
    0% {opacity: 0;}
    100% {opacity: 1;}
  }
  
  @keyframes fadeIn {
    0% {opacity: 0;}
    100% {opacity: 1;}
  }
  
  .fadeIn {
    -webkit-animation-name: fadeIn;
    animation-name: fadeIn;
  }
  
  .story-item {
    .remove-image {
      color: red;
      opacity: 0;
      -o-transition:.5s;
      -ms-transition:.5s;
      -moz-transition:.5s;
      -webkit-transition:.5s;
      transition:.5s;
    }
    .image:hover {
      .remove-image {
        opacity: 1;
        &:hover {
          cursor: pointer;
        }
      }
    }
  }